import React, { useState } from "react";
import axios from "axios";
import "./App.css";
import Recipe from "./components/Recipe";
import Alert from "./components/Alert";
import { v4 as uuidv4 } from "uuid";

const App = () => {
  const [search, setSearch] = useState("");
  const [recipes, setRecipes] = useState([]);
  const [alert, setAlert] = useState("");

  const APP_ID = "d52766a8";
  const APP_KEY = "1aecdb58e1ccc90577a2b650bc414e2b";

  const url = `https://api.edamam.com/search?q=${search}&app_id=${APP_ID}&app_key=${APP_KEY}&from=0&to=10&calories=591-722&health=alcohol-free`;

  const getData = async () => {
    if (search !== "") {
      const res = await axios.get(url);
      if (!res.data.more) {
        return setAlert("No Recipe found!");
      }
      setRecipes(res.data.hits);
      console.log(res);
      setAlert("");
      setSearch("");
    } else {
      setAlert("Please enter recipe to search!");
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    getData();
  };

  const handleChange = (e) => {
    setSearch(e.target.value);
  };

  return (
    <div className="App">
      <h1>Recipe Finder</h1>
      <form className="search-form" onSubmit={handleSubmit}>
        {alert !== "" && <Alert alert={alert} />}
        <input
          type="text"
          placeholder="Search Recipes"
          autoComplete="off"
          onChange={handleChange}
          value={search}
        />
        <input type="submit" value="search" />
      </form>

      <div className="recipes">
        {recipes !== [] &&
          recipes.map((recipe) => <Recipe key={uuidv4()} recipe={recipe} />)}
      </div>
    </div>
  );
};

export default App;
